import axiosInstance, { api } from "../../ultis/axios";

export interface MilestoneDto {
  _id: string;
  title: string;
  project_id: string;
  start_date: string;
  deadline: string;
  description?: string;
  status?: string;
  parent_id?: string | null;
}

export async function fetchMilestones(projectId: string): Promise<MilestoneDto[]> {
  const res = await api.get(`/api/projects/${projectId}/milestones`);
  return res.data as MilestoneDto[];
}


