"use client"

import { useEffect, useMemo, useState } from "react"
import { Card, CardContent } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { cn } from "@/lib/utils"
import { fetchMilestones, MilestoneDto } from "@/lib/api"

interface Props {
  projectId: string
}

interface Task {
  id: string
  name: string
  start: number
  duration: number
  progress: number
  status: "completed" | "in-progress" | "pending"
  assignee: string
}

function weeksBetween(start: Date, end: Date): number {
  const msInWeek = 1000 * 60 * 60 * 24 * 7
  const diff = Math.max(1, Math.ceil((end.getTime() - start.getTime()) / msInWeek))
  return diff
}

function mapMilestonesToTasks(milestones: MilestoneDto[], chartStart: Date): Task[] {
  return milestones.map((m) => {
    const start = new Date(m.start_date)
    const end = new Date(m.deadline)
    const msInWeek = 1000 * 60 * 60 * 24 * 7
    const startOffsetWeeks = Math.max(0, Math.floor((start.getTime() - chartStart.getTime()) / msInWeek))
    const durationWeeks = Math.max(1, Math.ceil((end.getTime() - start.getTime()) / msInWeek))
    return {
      id: m._id,
      name: m.title,
      start: startOffsetWeeks,
      duration: durationWeeks,
      progress: 0,
      status: (m.status as Task["status"]) || "pending",
      assignee: "",
    }
  })
}

export default function GanttChart({ projectId }: Props) {
  const [milestones, setMilestones] = useState<MilestoneDto[]>([])
  const [loading, setLoading] = useState(false)

  useEffect(() => {
    let ignore = false
    const load = async () => {
      try {
        setLoading(true)
        const data = await fetchMilestones(projectId)
        if (!ignore) setMilestones(data)
      } catch (e) {
        // eslint-disable-next-line no-console
        console.error(e)
      } finally {
        if (!ignore) setLoading(false)
      }
    }
    if (projectId) load()
    return () => {
      ignore = true
    }
  }, [projectId])

  const chartStart = useMemo(() => {
    if (milestones.length === 0) return new Date()
    const min = milestones.reduce((acc, cur) => {
      const d = new Date(cur.start_date)
      return d < acc ? d : acc
    }, new Date(milestones[0].start_date))
    return min
  }, [milestones])

  const chartEnd = useMemo(() => {
    if (milestones.length === 0) {
      const d = new Date(chartStart)
      d.setDate(d.getDate() + 7 * 10)
      return d
    }
    const max = milestones.reduce((acc, cur) => {
      const d = new Date(cur.deadline)
      return d > acc ? d : acc
    }, new Date(milestones[0].deadline))
    return max
  }, [milestones, chartStart])

  const totalWeeks = useMemo(() => {
    const w = weeksBetween(chartStart, chartEnd)
    return Math.max(10, w)
  }, [chartStart, chartEnd])

  const weeks = useMemo(() => Array.from({ length: totalWeeks }, (_, i) => `Week ${i + 1}`), [totalWeeks])

  const tasks = useMemo(() => mapMilestonesToTasks(milestones, chartStart), [milestones, chartStart])

  return (
    <Card className="w-full">
      <CardContent>
        <div className="overflow-x-auto">
          <div className="min-w-[800px]">
            {/* Header */}
            <div className="mb-4 grid grid-cols-[250px_1fr] gap-4">
              <div className="font-sans text-sm font-semibold text-foreground">Milestone Name</div>
              <div className="grid grid-cols-10 gap-1">
                {weeks.map((week, index) => (
                  <div key={index} className="text-center text-xs font-medium text-muted-foreground">
                    {week}
                  </div>
                ))}
              </div>
            </div>

            {/* Tasks */}
            <div className="space-y-3">
              {loading && <div className="text-sm text-muted-foreground">Loading...</div>}
              {!loading && tasks.length === 0 && (
                <div className="text-sm text-muted-foreground">No milestones</div>
              )}
              {tasks.map((task) => (
                <div key={task.id} className="grid grid-cols-[250px_1fr] gap-4">
                  {/* Task Info */}
                  <div className="flex flex-col justify-center gap-1">
                    <div className="font-sans text-sm font-medium text-foreground">{task.name}</div>
                    <div className="flex items-center gap-2">
                      <Badge
                        variant={
                          task.status === "completed"
                            ? "default"
                            : task.status === "in-progress"
                              ? "secondary"
                              : "outline"
                        }
                        className={cn(
                          "text-xs",
                          task.status === "completed" && "bg-primary text-primary-foreground",
                          task.status === "in-progress" && "bg-accent text-accent-foreground",
                          task.status === "pending" && "bg-muted text-muted-foreground",
                        )}
                      >
                        {task.status === "completed"
                          ? "Completed"
                          : task.status === "in-progress"
                            ? "In Progress"
                            : "Pending"}
                      </Badge>
                      <span className="text-xs text-muted-foreground">{task.assignee}</span>
                    </div>
                  </div>

                  {/* Timeline */}
                  <div className={cn("relative grid gap-1", `grid-cols-${10}`)}>
                    {/* Background grid */}
                    {weeks.map((_, index) => (
                      <div key={index} className="h-12 rounded-sm border border-border/50 bg-muted/20" />
                    ))}

                    {/* Task bar */}
                    <div
                      className="absolute top-0 h-12 rounded-md"
                      style={{
                        left: `${(task.start / totalWeeks) * 100}%`,
                        width: `${(task.duration / totalWeeks) * 100}%`,
                      }}
                    >
                      <div className="relative h-full overflow-hidden rounded-md bg-primary/20 ring-1 ring-primary/30">
                        {/* Progress bar */}
                        <div
                          className={cn(
                            "h-full transition-all duration-300",
                            task.status === "completed" && "bg-primary",
                            task.status === "in-progress" && "bg-accent",
                            task.status === "pending" && "bg-muted-foreground/30",
                          )}
                          style={{ width: `${task.progress}%` }}
                        />
                        {/* Progress text */}
                        <div className="absolute inset-0 flex items-center justify-center">
                          <span className="text-xs font-semibold text-foreground mix-blend-difference">
                            {task.progress}%
                          </span>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>

            {/* Legend */}
            <div className="mt-8 flex flex-wrap items-center gap-4 border-t border-border pt-4">
              <div className="text-sm font-medium text-foreground">Status:</div>
              <div className="flex items-center gap-2">
                <div className="h-3 w-3 rounded-sm bg-primary" />
                <span className="text-xs text-muted-foreground">Completed</span>
              </div>
              <div className="flex items-center gap-2">
                <div className="h-3 w-3 rounded-sm bg-accent" />
                <span className="text-xs text-muted-foreground">In Progress</span>
              </div>
              <div className="flex items-center gap-2">
                <div className="h-3 w-3 rounded-sm bg-muted-foreground/30" />
                <span className="text-xs text-muted-foreground">Pending</span>
              </div>
            </div>
          </div>
        </div>
      </CardContent>
    </Card>
  )
}
